import React from 'react'
import { Helmet } from 'react-helmet-async'
import HomeCover from './HomeCover'
import RecentlyAdded from './RecentlyAdded'
import FeaturedProductSlider from './FeturedProductSlider'


// The homepage We Binding All the Componant that Belongs to Home Page Sections
const Home = () => {
    return (
        <div>
            <Helmet><title>eCommerce</title></Helmet>
            <HomeCover />
            <RecentlyAdded /> {/*This is basically Flip the Deal Section*/}
            <FeaturedProductSlider />
        </div>
    )
}

export default Home