import { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import axios from "axios";


const SearchProduct = () => {

    // useParams hook is use to get the key from url for sending AJAX request to the server to searching product
    const { key } = useParams();

    // useState hook for managing the searched product and store on products
    const [products, setProducts] = useState([]);

    // useEffect hooks use to get searched DATA from backend and show instantly without reloading 
    useEffect(() => {
        axios.get(`http://localhost:3200/api/product/search/${key}`)
            .then(res => setProducts(res.data))
            .catch(err => console.log(err))
    }, [key]); //useEffect dependancy array insure that when any change are traced in key it will run again

    console.log(products)
    return (
        <div>
            <h2>Search Results for "{key}"</h2>

            <div className='d-flex flex-wrap justify-content-center text-center'>

                {products.length === 0 ? (<div className="alert alert-danger">No Result Found for {key}</div>)
                    : (
                        products.map((product) => {
                            return (
                                <div className=''>
                                    <div className='card card-size m-3'>
                                        <img src={product.img} alt="Item pic.." className='card-img-top' style={{ height: "300px" }} />
                                        <div className="card-body">
                                            <h5 className="card-title">{product.title}</h5>
                                            <span style={{ fontWeight: "bolder" }}>{product.price}</span>
                                            {/* <p className="card-text">{product.disc}</p> */}
                                        </div>
                                        <NavLink to={`/product/${product._id}`} type="button" className="btn-cart-allProduct">
                                            View</NavLink>

                                    </div>
                                </div>
                            )
                        })
                    )

                }

            </div>

        </div>
    );
};

export default SearchProduct;