import React, { useEffect, useReducer, } from 'react'
import axios from "axios"
import { NavLink, useParams } from 'react-router-dom'
import "./product.css"
import logger from 'use-reducer-logger';
import ErrorResp from '../components/ErrorStatus/ErrorResp';
import LoaderBox from '../components/Loading/LoaderBox';
import ErrorMsg from '../components/ErrorStatus/ErrorMsg';
import { Helmet } from 'react-helmet-async';

//Reducer Function To Fatching Data from backend
const reducerProductByMaleCategory = (state, action) => {
    switch (action.type) {
        case "REQUEST_DATA": {
            return { ...state, loading: true };
        }
        case "REQUEAT_DONE": {
            return { ...state, catProducts: action.payload, loading: false };
        }
        case "ERROR_REQUEST": {
            return { ...state, loading: false, error: action.payload }
        }
        default: {
            return state;
        }
    }
};

const MensAll = () => {

    //calling all the state from useReducer hook State manager instead of useState.
    //And using Logger to see the live calling logs of the product on console area
    const [{ loading, error, catProducts }, dispatch] = useReducer(logger(reducerProductByMaleCategory), {
        catProducts: [],//catProduct means category product
        loading: true,
        error: ""
    });

    //const [catProducts, setCatProducts] = useState([]);

    // using useParams hook to catch the value from url as category for helping to find the product category wise
    const { category } = useParams()

    // useEffect Hooks For fatching product data from server when application load or category matches
    useEffect(() => {

        const getProductByCat = async () => {

            // Dispatching Requests to reducer to match the case and update the state accordinglly.
            dispatch({ type: "REQUEST_DATA" });
            try {
                // Sending AJAX Req to the server to get the product as matches as category
                await axios.get(`http://localhost:3200/api/product?category=${category}`)
                    .then((res) => {
                        if (res) {
                            dispatch({ type: "REQUEAT_DONE", payload: res.data });
                        }
                    })
                    .catch((err) => dispatch({ type: "ERROR_REQUEST", payload: ErrorResp(err) }));
            } catch (error) {
                console.log(error);
            }

        }
        getProductByCat()

    }, [category]);//useEffect array dependency insure when category changes useEffect run again

    return (
        <>

            {/* Using Helmet Lib to show the Name of the active page on web-page title bar */}
            <Helmet>
                <title>Mans {category === "shirt" ? "Shirts" : category === "paint" ? "Paints" : "All Product"}</title>
            </Helmet>

            {/* Here we showing heading like Mans and in which category we have right now */}
            <h3 className='fw-bold text-center mt-3'>Mans {category === "shirt" ? "Shirts" : category === "paint" ? "Paints" : "All Product"}</h3>
            <div className='d-flex flex-wrap justify-content-center text-center'>

                {

                    // if state is in loading action execute this code
                    loading ? (
                        <LoaderBox />//this component is defined for showing loading gif.
                    ) :
                        // if state is going to an error action execute this code
                        error ? (
                            <ErrorMsg className="alert alert-warning" role="alert">
                                {error}
                            </ErrorMsg>//this component is defined for showing error msg.
                        ) : (

                            // if state is in success action execute this code.

                            // If Product not Avaliable to respective category show this msg otherwise run > : part 
                            catProducts.length === 0
                                ?
                                <span className='alert alert-dark' role="alert">
                                    Product Not Available Right Know It will be Introduce Soon
                                </span>

                                :
                                // If Product Avaliable to respective category
                                catProducts.map((product) => {
                                    return (
                                        <div className='' key={product._id}>
                                            <div className='card card-size m-3'>
                                                <img src={product.img} alt="Item pic.." className='card-img-top' style={{ height: "300px" }} />
                                                <div className="card-body">
                                                    <h5 className="card-title">{product.title.slice(0, 24)}</h5>
                                                    <span style={{ fontWeight: "bolder" }}>{product.price}$</span>
                                                    {/* <p className="card-text">{product.disc}</p> */}
                                                </div>
                                                <NavLink to={`/product/${product._id}`} type="button" className="btn-cart-allProduct">
                                                    View</NavLink>

                                            </div>

                                        </div>
                                    )
                                })
                        )
                }

            </div>
        </>
    )
}

export default MensAll